/* extra pack attack form styles*/

div.title {
  justify-content: left;
  text-align: left;
}

div.attack_form {
  justify-content: left;
  text-align: left;
}

div.results {
  justify-content: left;
  text-align: left;
}

div.scroll_outer {
  margin: 0 auto;
  background: url('img/scroll-png-26394.png');
  background-position: left;
  background-repeat: no-repeat;
  height: auto;
  display: grid;
  padding-top: 188px;
  padding-bottom: 95px;
  padding-left: 88px;
}

div.scroll_contents {
  justify-content: center;
  text-align: left;
  max-width: 800px;
  max-height: 700px;
}

div.scroll_inner {
  margin: 0 auto;
}

div.scroll_action {
  text-align: center;
}

/* formik */

div.columns {
  width: 900px;
}
div.columns div {
  width: 250px;
  height: 50px;
  float: left;
}
div.clear {
  clear: both;
}

* {
  box-sizing: border-box;
  font-family: ;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial,
    sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-size: 14px;
  line-height: 1.5;
  color: #24292e;
  background-color: #fff;
}

.app {
  margin: 2rem;
}

a {
  color: #08c;
}

code {
  background: #eee;
  padding: 0.1rem;
  font-family: "Menlo";
  font-size: 13px;
  color: #ff00aa;
}

.text-input {
  padding: 0.5rem;
  font-size: 16px;
  width: 100%;
  display: block;
  border-radius: 4px;
  border: 1px solid #ccc;
}

.text-input:focus {
  border-color: #007eff;
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075),
    0 0 0 3px rgba(0, 126, 255, 0.1);
  outline: none;
}

.error .text-input {
  border-color: red;
}

.label {
  font-weight: bold;
  display: block;
  margin-bottom: 0.5rem;
}

.error .label {
  color: red;
}

.input-feedback {
  color: #999;
  margin-top: 0.25rem;
}

.error .input-feedback {
  color: red;
}

.animated {
  animation-duration: 0.3s;
  animation-fill-mode: both;
}

.input-group {
  margin-bottom: 1rem;
}

button {
  max-width: 150px;
  margin: 20px 0;
  padding: 12px 20px;
  border-style: none;
  border-radius: 5px;
  background-color: #08c;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
  font-size: 17px;
  font-weight: 500;
  color: #fff;
  cursor: pointer;
  outline: none;
  -webkit-appearance: none;
}

button:disabled {
  opacity: 0.5;
  cursor: not-allowed !important;
}

button + button {
  margin-left: 0.5rem;
}

button.outline {
  background-color: #eee;
  border: 1px solid #aaa;
  color: #555;
}

.animated {
  animation-duration: 1s;
  animation-fill-mode: both;
}

@keyframes shake {
  from,
  to {
    transform: translate3d(0, 0, 0);
  }

  10%,
  30%,
  50%,
  70%,
  90% {
    transform: translate3d(-10px, 0, 0);
  }

  20%,
  40%,
  60%,
  80% {
    transform: translate3d(10px, 0, 0);
  }
}

.shake {
  animation-name: shake;
}
